<template>
    <div class="container-fluid">
        <!--  -->
        <div v-if="!isStop && cell.contentType === CONTENT_TYPE[0].value" id="carouselExampleIndicators" class="col-12 carousel slide" data-ride="carousel" data-interval="false">
            <div class="carousel-inner">
                <div v-for="content in cell.contents" class="carousel-item" :class="{active: content.isActive}">
                    <!-- <img class="d-block w-100" :src="content.contentPath + '/' + content.contentName"/> -->
                    <img class="d-block w-100" :src="content.contentPath + '/' + content.contentName"/>
                </div>
            </div>
            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next" style="display: none;" ref="nextImgBtn">
                <span class="carousel-control-next-icon" aria-hidden="true"></span>
                <span class="sr-only">Next</span>
            </a>
        </div>
        <!--  -->
        <div v-else-if="!isStop && cell.contentType === CONTENT_TYPE[1].value" id="carouselExampleIndicators" class="col-12 carousel slide" data-ride="carousel" data-interval="false">
            <video class="d-block w-100" ref="video" @ended="videoPlay" />
        </div>
        <!--  -->
        <div v-else-if="!isStop && cell.contentType === CONTENT_TYPE[2].value" id="carouselExampleIndicators" class="col-12 carousel slide" data-ride="carousel" data-interval="false" style="top: 50%;">
            <marquee style="font-size: 200px; font-weight: bold;">{{sub}}</marquee>
        </div>
        <!-- URL -->
        <div
            v-else-if="!isStop && cell.contentType === CONTENT_TYPE[3].value"
            id="carouselExampleIndicators"
            class="col-12 carousel slide"
            data-ride="carousel"
            data-interval="false"
            style="height: 100%;">
            <iframe ref="iframe" style="height: 100%; width: 100%;"/>
        </div>
    </div>
</template>

<script>
    import request from '../../../lib/request'

    import CONTENT_TYPE from '../../code/contentType'

    const IMAGE_INTERVAL = 20000,
        URL_INTERVAL = 20000

    export default {
        data () {
            return {
                CONTENT_TYPE: CONTENT_TYPE,
                cell: {},
                currentPlayIndex: -1,
                currentUrlIndex: -1,
                imageInterval: null,
                urlInterval: null,
                isStop: false
            }
        },
        props: {
            clientId: {
                type: Number,
                require: true
            },
            boardIndex: {
                type: Number,
                require: true
            },
            templateType: {
                type: Number,
                require: true
            },
            urgentYn: {
                type: String,
                require: 'N'
            }  
        },
        computed: {
            sub () {
                return this.cell.contents.reduce(
                    (sub, content) => {
                        return sub + '\t' + content.contentInfo
                    },
                    '')
            }
        },
        methods: {
            videoPlay () {
                this.$log.debug('video play')
                this.currentPlayIndex = this.cell.contents[this.currentPlayIndex + 1] ? this.currentPlayIndex + 1 : 0
                this.$refs.video.src = this.cell.contents[this.currentPlayIndex].contentPath + '/' + this.cell.contents[this.currentPlayIndex].contentName
                var playPromise = this.$refs.video.play()
				  if (playPromise !== undefined) {
				    playPromise.then(_ => {
				      // Automatic playback started!
				      // Show playing UI.
				      
				    })
				    .catch(error => {
				      // Auto-play was prevented
				      // Show paused UI.
				      console.log(error)
				    });
				  }
            },
            videoPause () {
                this.$log.debug('video pause')
                this.$refs.video.pause()
            },
            startImageInterval () {
                this.imageInterval = setInterval(
                    () => {
                        this.$refs.nextImgBtn.click()
                    },
                    IMAGE_INTERVAL)
            },
            stopImageInterval () {
                clearInterval(this.imageInterval)
                this.imageInterval = null
            },
            startUrlInterval () {
                this.urlInterval = setInterval(
                    () => this.nextUrl(),
                    URL_INTERVAL)
            },
            stopUrlInterval () {
                clearInterval(this.urlInterval)
                this.urlInterval = null
            },
            stop () {
                this.$log.debug('stop')

                switch (this.cell.contentType) {
                    case CONTENT_TYPE[0].value:
                        this.stopImageInterval()
                        break
                    case CONTENT_TYPE[1].value:
                        this.videoPause()
                        break
                    case CONTENT_TYPE[3].value:
                        this.stopUrlInterval()
                        break
                }

                this.isStop = true
                this.$sendClientInfo('N')
            },
            play () {
                this.$log.debug('play')
                this.isStop = false

                switch (this.cell.contentType) {
                    case CONTENT_TYPE[0].value:
                        this.startImageInterval()
                        break
                    case CONTENT_TYPE[1].value:
                        setTimeout(() => this.videoPlay(), 5000)
                        break
                    case CONTENT_TYPE[3].value:
                        setTimeout(
                            () => {
                                this.nextUrl()
                                this.startUrlInterval()
                            },
                            3000)
                        break
                }

                this.$sendClientInfo('Y')
            },
            nextUrl () {
                this.$log.debug('nextUrl')
                this.currentUrlIndex = this.cell.contents[this.currentUrlIndex + 1] ? this.currentUrlIndex + 1 : 0
                this.$refs.iframe.src = this.cell.contents[this.currentUrlIndex].contentInfo
            }
        },
        mounted () {
          $(document).ready(function(){
            $(document).bind("contextmenu", function(e) {
              return false;
            });
          });
          $(document)[0].oncontextmenu = function() { return false; }

            this.$log.debug('clientId : ', this.clientId, ', boardIndex :', this.boardIndex, ', urgentYn :', this.urgentYn)
            //img, mov, sub, url
            request.client.getBoardInfo(this.clientId, this.boardIndex, this.templateType, this.urgentYn)
                .then(result => {
                    this.$log.debug('get board info ', result)

                    if (result.data.result === 200) {
                        if (/^N$/i.test(result.data.isHolidayOff)) {
                            let contentType = CONTENT_TYPE.find(contentType => Array.isArray(result.data.data[contentType.key]) && result.data.data[contentType.key].length > 0)

                            if (contentType) {
                                this.cell = {
                                    contentType: contentType.value,
                                    contents: result.data.data[contentType.key]
                                }

                                switch (contentType.value) {
                                    case CONTENT_TYPE[0].value:
                                        this.cell.contents.forEach((content, index) => {
                                            content.isActive = index === 0
                                        })
                                        break
                                }

                                this.play()
                            } else {
                                //   
                            }

                            this.$log.debug('!!!! ', this.cell)
                        } else {
                            this.$log.debug('')
                        }
                    } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    alert('  .\n' + err.message)
                })
        }
    }
</script>

