<script>
    import request from '../../../lib/request'

    import CONTENT_TYPE from '../../code/contentType'

    const IMAGE_INTERVAL = 20000,
        URL_INTERVAL = 20000

    export default {
        data () {
            return {
                CONTENT_TYPE: CONTENT_TYPE,
                currentPlayIndex: -1,
                currentUrlndex: -1,
                cells: [],
                imageInterval: null,
                urlInterval: null,
                isStop: false
            }
        },
        props: {
            clientId: {
                type: Number,
                require: true
            },
            boardIndex: {
                type: Number,
                require: true
            },
            templateType: {
                type: Number,
                require: true
            },
            urgentYn: {
                type: String,
                require: 'N'
            }
        },
        methods: {
            videoPlay () {
                this.$log.debug('video play')

                this.cells.some((cell, index) => {
                    if (cell.contentType === CONTENT_TYPE[1].value) {
                        this.currentPlayIndex = cell.contents[this.currentPlayIndex + 1] ? this.currentPlayIndex + 1 : 0
                        this.$refs['video' + (index + 1)].src = cell.contents[this.currentPlayIndex].contentPath + '/' + cell.contents[this.currentPlayIndex].contentName
                        setTimeout(() => this.$refs['video' + (index + 1)].play(), 1000)

                        return true
                    }
                })
            },
            videoPause () {
                this.cells.some((cell, index) => {
                    if (cell.contentType === CONTENT_TYPE[1].value) {
                        this.$refs['video' + (index + 1)].pause()

                        return true
                    }
                })
            },
            startImageInterval () {
                this.cells.some((cell, index) => {
                    if (cell.contentType === CONTENT_TYPE[0].value) {
                        this.imageInterval = setInterval(
                            () => {
                                this.$refs['nextImgBtn' + (index + 1)].click()
                            },
                            IMAGE_INTERVAL)

                        return true
                    }
                })
            },
            stopImageInterval () {
                clearInterval(this.imageInterval)
                this.imageInterval = null
            },
            startUrlInterval () {
                this.cells.some((cell, index) => {
                    if (cell.contentType === CONTENT_TYPE[3].value) {
                        this.urlInterval = setInterval(
                            () => {
                                this.nextUrl()
                            },
                            URL_INTERVAL)

                        return true
                    }
                })
            },
            stopUrlInterval () {
                clearInterval(this.urlInterval)
                this.urlInterval = null
            },
            getSub (contents) {
                return contents.reduce(
                    (sub, content) => {
                        return sub + '\t' + content.contentInfo
                    },
                    '')
            },
            stop () {
                this.$log.debug('stop')

                this.cells.forEach(cell => {
                    switch (cell.contentType) {
                        case CONTENT_TYPE[0].value:
                            this.stopImageInterval()
                            break
                        case CONTENT_TYPE[1].value:
                            this.videoPause()
                            break
                        case CONTENT_TYPE[3].value:
                            this.stopUrlInterval()
                            break
                    }
                })

                this.isStop = true

                this.$sendClientInfo('N')
            },
            play () {
                this.$log.debug('play')
                this.isStop = false

                this.cells.forEach(cell => {
                    switch (cell.contentType) {
                        case CONTENT_TYPE[0].value:
                            this.startImageInterval()
                            break
                        case CONTENT_TYPE[1].value:
                            setTimeout(() => this.videoPlay(), 5000)
                            break
                        case CONTENT_TYPE[3].value:
                            setTimeout(
                                () => {
                                    this.nextUrl()
                                    this.startUrlInterval()
                                },
                                3000)
                            break
                    }
                })

                this.$sendClientInfo('Y')
            },
            nextUrl () {
                this.$log.debug('nextUrl')

                this.cells.some((cell, index) => {
                    if (cell.contentType === CONTENT_TYPE[3].value) {
                        this.currentUrlIndex = cell.contents[this.currentUrlIndex + 1] ? this.currentUrlIndex + 1 : 0
                        this.$refs['iframe' + (index + 1)].src = cell.contents[this.currentUrlIndex].contentInfo

                        return true
                    }
                })
            }
        },
        mounted () {
          $(document).ready(function(){
            $(document).bind("contextmenu", function(e) {
              return false;
            });
          });
          $(document)[0].oncontextmenu = function() { return false; }

            this.$log.debug('clientId : ', this.clientId, ', boardIndex :', this.boardIndex, ', urgentYn :', this.urgentYn)
            //img, mov, sub, url
            request.client.getBoardInfo(this.clientId, this.boardIndex, this.templateType, this.urgentYn)
                .then(result => {
                    this.$log.debug('get board info ', result)

                    if (result.data.result === 200) {
                        if (/^N$/i.test(result.data.isHolidayOff)) {
                            CONTENT_TYPE.some(contentType => {
                                if (Array.isArray(result.data.data[contentType.key]) && result.data.data[contentType.key].length > 0) {
                                    this.cells.push({
                                        contentType: contentType.value,
                                        contents: result.data.data[contentType.key]
                                    })

                                    if (this.cells.length === 4) {
                                        return true
                                    }
                                }
                            });

                            (function init (index) {
                                if (this.cells[index]) {
                                    switch (this.cells[index].contentType) {
                                        case CONTENT_TYPE[0].value:
                                            this.cells[index].contents.forEach((content, index) => {
                                                content.isActive = index === 0
                                            })

                                            break
                                    }

                                    init.call(this, index + 1)
                                }
                            }).call(this, 0)

                            this.$log.debug('!!!! ', this.cells)
                            this.play()
                        } else {
                            this.$log.debug('')
                        }
                    } else {
                        throw new Error(result.data.resultMsg)
                    }
                })
                .catch(err => {
                    alert('  .\n' + err.message)
                })
        }
    }
</script>

