import Vue from 'vue'
import VueLogger from 'vuejs-logger'

/**
 * @since       0.0.1
 * @desc        log <br/>
 *              log levels
 *                  * debug
 *                  * info
 *                  * warn
 *                  * error
 *                  * fatal
 * @author      
 * @see         {@link https://www.npmjs.com/package/vuejs-logger vuejs-logger}
 */

Vue.use(
    VueLogger,
    {
        logLevel : 'info',
        stringifyArguments : false, // true     JSON.stringify ()  
        showLogLevel : true,        // true   loglevel 
        separator: '|',             //  
        showConsoleColors: true     // true    loglevels  console.warn, console.fatal, console.error 
    })
