<template>
    <div class="app-body">
        <type1 v-if="templateType == TEMPLATE_TYPE[0].value" :client-id="clientId" :board-index="boardIndex" :template-type="templateType" :urgentYn="urgentYn" ref="type1"></type1>
        <type2 v-else-if="templateType == TEMPLATE_TYPE[1].value" :client-id="clientId" :board-index="boardIndex" :template-type="templateType" :urgentYn="urgentYn" ref="type2"></type2>
        <type3 v-else-if="templateType == TEMPLATE_TYPE[2].value" :client-id="clientId" :board-index="boardIndex" :template-type="templateType" :urgentYn="urgentYn" ref="type3"></type3>
        <type4 v-else-if="templateType == TEMPLATE_TYPE[3].value" :client-id="clientId" :board-index="boardIndex" :template-type="templateType" :urgentYn="urgentYn" ref="type4"></type4>
    </div>
</template>

<script>
    import qs from 'querystringify'

    import Type1 from '../../../components/template/promotion/Type1.vue'
    import Type2 from '../../../components/template/promotion/Type2.vue'
    import Type3 from '../../../components/template/promotion/Type3.vue'
    import Type4 from '../../../components/template/promotion/Type4.vue'

    import TEMPLATE_TYPE from '../../../components/code/templateType'

    const ext = window.external

    export default {
        name: 'app',
        components: {
            Type1,
            Type2,
            Type3,
            Type4
        },
        data () {
            return {
                TEMPLATE_TYPE: TEMPLATE_TYPE,
                templateType: '',
                clientId: '',
                boardIndex: 0,
                urgentYn: 'N'
            }
        },
        methods: {
          callPlayFromApp()
          {
            {
                this.$log.debug('play')

                switch (this.$templateType) {
                    case TEMPLATE_TYPE[0].value:
                        this.$refs.type1.play()
                        break
                    case TEMPLATE_TYPE[1].value:
                        this.$refs.type2.play()
                        break
                    case TEMPLATE_TYPE[2].value:
                        this.$refs.type3.play()
                        break
                    case TEMPLATE_TYPE[3].value:
                        this.$refs.type4.play()
                        break
                }
            }
          },
          callStopFromApp()
          {
            {
                this.$log.debug('stop')

                switch (this.$templateType) {
                    case TEMPLATE_TYPE[0].value:
                        this.$refs.type1.stop()
                        break
                    case TEMPLATE_TYPE[1].value:
                        this.$refs.type2.stop()
                        break
                    case TEMPLATE_TYPE[2].value:
                        this.$refs.type3.stop()
                        break
                    case TEMPLATE_TYPE[3].value:
                        this.$refs.type4.stop()
                        break
                }
            }
          },
            setSocketEvents () {
                this.$socket
                    .on('poweroff', data => {
                        this.$log.debug('poweroff ', data)

                        try {
                            if (data.clientId == this.$clientId) {
                                ext.systemOff()
                            } else {
                                this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                                this.$socket.emit('templateError', 'clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                            }
                        } catch (err) {
                            this.$socket.emit('templateError', err.message)
                        }
                    })
                    .on('reboot', data => {
                        this.$log.debug('reboot ', data)

                        if (data.clientId == this.$clientId) {
                            ext.systemReboot()
                        } else {
                            this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }
                    })
                    .on('urgent', data => {
                        this.$log.debug('urgent ', data)

                        if (data.clientId == this.$clientId) {
                            ext.urgentEvent(this.boardIndex)
                        } else {
                            this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }
                    })
                    .on('play', data => {
                        this.$log.debug('play ', data)

                        if (data.clientId == this.$clientId) {
                            ext.playEvent(this.boardIndex)
                        } else {
                            this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }

                        // if (data.clientId == this.$clientId) {
                        //     this.$log.debug('play')
                        //
                        //     switch (this.$templateType) {
                        //         case TEMPLATE_TYPE[0].value:
                        //             this.$refs.type1.play()
                        //             break
                        //         case TEMPLATE_TYPE[1].value:
                        //             this.$refs.type2.play()
                        //             break
                        //         case TEMPLATE_TYPE[2].value:
                        //             this.$refs.type3.play()
                        //             break
                        //         case TEMPLATE_TYPE[3].value:
                        //             this.$refs.type4.play()
                        //             break
                        //     }
                        // } else {
                        //     this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        // }
                    })
                    .on('stop', data => {
                        this.$log.debug('stop ', data)

                        if (data.clientId == this.$clientId) {
                            ext.stopEvent(this.boardIndex)
                        } else {
                            this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        }

                        // if (data.clientId == this.$clientId) {
                        //     this.$log.debug('stop')
                        //
                        //     switch (this.$templateType) {
                        //         case TEMPLATE_TYPE[0].value:
                        //             this.$refs.type1.stop()
                        //             break
                        //         case TEMPLATE_TYPE[1].value:
                        //             this.$refs.type2.stop()
                        //             break
                        //         case TEMPLATE_TYPE[2].value:
                        //             this.$refs.type3.stop()
                        //             break
                        //         case TEMPLATE_TYPE[3].value:
                        //             this.$refs.type4.stop()
                        //             break
                        //     }
                        // } else {
                        //     this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                        // }
                    })
                    .on('getLocalIp', data => {
                        this.$log.debug('getLocalIp')

                        try {
                            if (data.clientId == this.$clientId) {
                                this.$socket.emit('getLocalIp', ext.getLocalIp())
                            } else {
                                this.$log.debug('clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                                this.$socket.emit('templateError', 'clientId  . ($clientId : ' + this.$clientId + ', data.clientId : ' + data.clientId + ')')
                            }
                        } catch (err) {
                            this.$socket.emit('templateError', err.message)
                        }
                    })

                this.$log.debug('set socket events')
            }
        },
        created () {
            let parsedQs = qs.parse(location.search)

            this.templateType = parsedQs.templateType
            this.clientId = parsedQs.clientId
            this.boardIndex = parsedQs.boardIndex
            this.urgentYn = parsedQs.urgentYn

            this.setSocketEvents()

            this.$log.debug(this.templateType, this.clientId, this.boardIndex, this.urgentYn)
        }
    }
</script>

